import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons'
import { Link } from 'react-router-dom'

export const Obtain = () => {
    return (
        <div>
            <h2 style={{fontSize: '2rem', fontWeight: '300'}}>Tres sencillos pasos para disponer del dinero en tu cuenta de la manera ms conveniente!</h2>
            <ol className='numbers-work'>
                <li><p><strong>Elige el monto, periodicidad de pago y plazo en el simulador, luego presiona SOLICITALO YA</strong></p></li>
                <li><p><strong>Contina llenando el formulario de registro. </strong>No te preocupes! Te vamos a hacer varias preguntas respecto a tu informacin personal y bancaria. Esta informacin es sumamente importante para poder brindarte el prstamo de forma rpida y segura. Toda la informacin es confidencial y slo ser utilizada para validar tu prstamo.</p></li>
                <li><p>Luego de brindar toda la informacin solicitada, <strong>espera unos momentos por nuestra confirmacin.</strong> Si la solicitud es aprobada, sers informado en tu perfil en Vivus.com.mx y tambin mediante un correo electrnico y mensaje de texto. El dinero solicitado ser transferido a tu cuenta bancaria o en efectivo a la brevedad.</p></li>
            </ol>
            <hr style={{width: '80%', border: '0.5px solid #737373', margin: '2rem auto'}}/>
            <div className="note">
                <div className='note-left'>
                    <FontAwesomeIcon icon={faExclamationCircle}/>
                </div>
                <div className='note-right'>
                    Si ya ests registrado previamente en Vivus.com.mx <Link to='/login' style={{color:'#A3CD3A'}}>Ingresar</Link> en tu perfil utilizando tu correo electrnico, contrasea y respetando las guas disponibles para solicitar tu prstamo por primera vez o prstamos subsecuentes. <br/> Si ya tienes un prstamo con nosotros, te podemos ofrecer un monto adicional o recorrer la fecha de pago de tu parcialidad.
                </div>
            </div>
        </div>
    )
}
